import React from 'react'

import './Wrapper.css'

const Wrapper = (props) => {
  const {
    children,
    className,
    large
  } = props;

  return (
    <div className={'wrapper ' + (className ? className : '') + (large ? ' wrapper_large' : '')}>
      {children}
    </div>
  )
}

export default Wrapper;
