import React from 'react'
import Wrapper from '../subcomponents/wrapper/Wrapper'

import './Header.css'

const Header = (props) => {
  return (
    <header className='header'>
      <Wrapper>
        <div className='header__container'>
          <h1 className='header__title'>   <span></span></h1>
          <div className='header__sub-title'>     </div>
          <button className='header__btn'>   </button>
        </div>
      </Wrapper>
    </header>
  )
}

export default Header;