import React from 'react'
import Wrapper from '../subcomponents/wrapper/Wrapper'
import TraceForm from '../subcomponents/trace-form/TraceForm'
import Letter from '../../images/track/letter.png'
import { ReactComponent as AppBtn } from '../../svg/track/apple.svg'
import { ReactComponent as GoogleBtn } from '../../svg/track/google.svg'

import './Track.css'

const Track = (props) => {
  return (
    <div className='track'>
      <Wrapper 
      className='track__container'
      >
        <div className="track__letter-bg">
          <img src={Letter} alt="letter" className='track__letter'/>
        </div>
        <div className="track__content">
          <div className="track__title">
             
          </div>
          <div className="track__sub-title">
              ,   
          </div>
          <TraceForm className='track__trace-form'/>
          <div className='track__mobile'>
                   
          </div>
          <div>
            <button className='track__mobile-btn'>
              <AppBtn/>
            </button>
            <button className='track__mobile-btn'>
              <GoogleBtn/>
            </button>
          </div>
        </div>
      </Wrapper>
    </div>
  )
}

export default Track;
