import React from 'react'
import Slider from 'react-slick'
import ItemImg1 from '../../images/services/servicesItem1.jpg'
import ItemImg2 from '../../images/services/servicesItem2.jpg'
import ItemImg3 from '../../images/services/servicesItem3.jpg'
import ItemImg4 from '../../images/services/servicesItem4.jpg'
import ItemImgMedium1 from '../../images/services/1024/servicesItem1.jpg'
import ItemImgMedium2 from '../../images/services/1024/servicesItem2.jpg'
import ItemImgMedium3 from '../../images/services/1024/servicesItem3.jpg'
import ItemImgMedium4 from '../../images/services/1024/servicesItem4.jpg'
import { ReactComponent as Arrow } from '../../svg/arrow.svg'

import "./Services.css"
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Services = (props) => {
  const settings = {
    dots: false,
    arrows: false,
    draggable: true,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
    initialSlide: 0,
    variableWidth: true,
    responsive: [
      {
        breakpoint: 660,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        }
      },
      {
        breakpoint: 440,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]
  };
  return (
    <div className="services">
      <div className="services__item-container">
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
            <source srcSet={ItemImgMedium1} media="(max-width: 1565px)"/>
            <img src={ItemImg1} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
              
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">   , ,      ..</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium2} media="(max-width: 1565px)"/>
              <img src={ItemImg2} alt="item-bg"/>
          </picture>
          <div className="services__item-title services__item-title_width">
            ,    -
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">, , -, -    ..</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium3} media="(max-width: 1565px)"/>
              <img src={ItemImg3} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
            -    
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">      . ,   .</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium4} media="(max-width: 1565px)"/>
              <img src={ItemImg4} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
             
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">        .</div>
            <Arrow/>
          </div>
        </a>
      </div>
      <div className="services__container-carousel">
        <Slider {...settings}>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
            <source srcSet={ItemImgMedium1} media="(max-width: 1565px)"/>
            <img src={ItemImg1} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
              
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">   , ,      ..</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium2} media="(max-width: 1565px)"/>
              <img src={ItemImg2} alt="item-bg"/>
          </picture>
          <div className="services__item-title services__item-title_width">
            ,    -
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">, , -, -    ..</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium3} media="(max-width: 1565px)"/>
              <img src={ItemImg3} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
            -    
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">      . ,   .</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item">
          <picture className='services__item-bg'>
              <source srcSet={ItemImgMedium4} media="(max-width: 1565px)"/>
              <img src={ItemImg4} alt="item-bg"/>
          </picture>
          <div className="services__item-title">
             
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">        .</div>
            <Arrow/>
          </div>
        </a>
        </Slider>
      </div>
    </div>
  )
}

export default Services;