import React from 'react'
import { Link } from 'react-router-dom'

import Wrapper from '../subcomponents/wrapper/Wrapper'
import TraceForm from '../subcomponents/trace-form/TraceForm'
import Logo from '../../images/logo.png'
import LogoXl from '../../images/logo1024.png'
import { ReactComponent as LogoLc } from '../../svg/lc.svg'

import './NavigationBar.css'

const NavigationBar = (props) => {
  return (
    <div className='navigation'>
      <Wrapper large={true}>
        <div className="navigation__container">
          <div className='navigation__container-flex'>
            <a href="/" className='navigation__logo'>
              <picture >
                <source srcSet={LogoXl} media="(max-width: 1470px)"/>
                <img src={Logo} alt="logo"/>
              </picture>
            </a>
            <TraceForm />
          </div>
          <div className='navigation__container-flex'>
            <div className='navigation__links'>
              <a href='/services'></a>
              <a href='/vacancies'></a>
              <a href='/contacts'></a>
            </div>
            <a href='/lc' className='navigation__lc'>
              <LogoLc/>
               
            </a>
          </div>
        </div>
        <div className="navigation__burger-btn"></div>
      </Wrapper>
    </div>
  )
}

export default NavigationBar;
