import React from 'react'
import Slider from 'react-slick'
import Wrapper from '../subcomponents/wrapper/Wrapper'
import Item1 from '../../images/clients/item1.png'
import Item2 from '../../images/clients/item2.png'
import Item3 from '../../images/clients/item3.png'
import Item4 from '../../images/clients/item4.png'
import Item5 from '../../images/clients/item5.png'
import Item6 from '../../images/clients/item6.png'
import Item7 from '../../images/clients/item7.png'
import Item8 from '../../images/clients/item8.png'
import Item9 from '../../images/clients/item9.png'
import Item10 from '../../images/clients/item10.png'

import Item1Medium from '../../images/clients/1024/item1.png'
import Item2Medium from '../../images/clients/1024/item2.png'
import Item3Medium from '../../images/clients/1024/item3.png'
import Item4Medium from '../../images/clients/1024/item4.png'
import Item5Medium from '../../images/clients/1024/item5.png'
import Item6Medium from '../../images/clients/1024/item6.png'
import Item7Medium from '../../images/clients/1024/item7.png'
import Item8Medium from '../../images/clients/1024/item8.png'
import Item9Medium from '../../images/clients/1024/item9.png'
import Item10Medium from '../../images/clients/1024/item10.png'

import Item1Mobile from '../../images/clients/320/item1.png'
import Item2Mobile from '../../images/clients/320/item2.png'
import Item3Mobile from '../../images/clients/320/item3.png'
import Item4Mobile from '../../images/clients/320/item4.png'
import Item5Mobile from '../../images/clients/320/item5.png'
import Item6Mobile from '../../images/clients/320/item6.png'
import Item7Mobile from '../../images/clients/320/item7.png'
import Item8Mobile from '../../images/clients/320/item8.png'
import Item9Mobile from '../../images/clients/320/item9.png'
import Item10Mobile from '../../images/clients/320/item10.png'


import './Clients.css'

const Clients = (props) => {
  const settings = {
    dots: true,
    arrows: false,
    draggable: true,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 660,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 440,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]
  };
  return (
    <div className="clients">
      <Wrapper>
        <div className="clients__title">
             
        </div>
        <div className="clients__container">
          <div className="clients__item">
            <picture>
              <source srcSet={Item1Medium} media="(max-width: 1533px)"/>
              <img src={Item1} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item2Medium} media="(max-width: 1533px)"/>
              <img src={Item2} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item3Medium} media="(max-width: 1533px)"/>
              <img src={Item3} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item4Medium} media="(max-width: 1533px)"/>
              <img src={Item4} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item5Medium} media="(max-width: 1533px)"/>
              <img src={Item5} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item6Medium} media="(max-width: 1533px)"/>
              <img src={Item6} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item7Medium} media="(max-width: 1533px)"/>
              <img src={Item7} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item8Medium} media="(max-width: 1533px)"/>
              <img src={Item8} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item9Medium} media="(max-width: 1533px)"/>
              <img src={Item9} alt="item"/>
            </picture>
          </div>
          <div className="clients__item">
            <picture>
              <source srcSet={Item10Medium} media="(max-width: 1533px)"/>
              <img src={Item10} alt="item"/>
            </picture>
          </div>
        </div>
        <div className="clients__container-slider">
          <Slider {...settings}>
            <div className="clients__slide">
              <div className="clients__item">
                  <picture>
                    <source srcSet={Item1Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item1Medium} media="(max-width: 1533px)"/>
                    <img src={Item1} alt="item"/>
                  </picture>
                </div>
                <div className="clients__item">
                  <picture>
                    <source srcSet={Item2Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item2Medium} media="(max-width: 1533px)"/>
                    <img src={Item2} alt="item"/>
                  </picture>
                </div>
                <div className="clients__item">
                  <picture>
                    <source srcSet={Item3Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item3Medium} media="(max-width: 1533px)"/>
                    <img src={Item3} alt="item"/>
                  </picture>
                </div>
                <div className="clients__item">
                  <picture>
                    <source srcSet={Item4Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item4Medium} media="(max-width: 1533px)"/>
                    <img src={Item4} alt="item"/>
                  </picture>
                </div>
                <div className="clients__item">
                  <picture>
                    <source srcSet={Item5Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item5Medium} media="(max-width: 1533px)"/>
                    <img src={Item5} alt="item"/>
                  </picture>
                </div>
                <div className="clients__item">
                  <picture>
                    <source srcSet={Item6Mobile} media="(max-width: 912px)"/>
                    <source srcSet={Item6Medium} media="(max-width: 1533px)"/>
                    <img src={Item6} alt="item"/>
                  </picture>
                </div>
            </div>
            <div className="clients__slide">
              <div className="clients__item">
                <picture>
                  <source srcSet={Item7Mobile} media="(max-width: 912px)"/>
                  <source srcSet={Item7Medium} media="(max-width: 1533px)"/>
                  <img src={Item7} alt="item"/>
                </picture>
              </div>
              <div className="clients__item">
                <picture>
                  <source srcSet={Item8Mobile} media="(max-width: 912px)"/>
                  <source srcSet={Item8Medium} media="(max-width: 1533px)"/>
                  <img src={Item8} alt="item"/>
                </picture>
              </div>
              <div className="clients__item">
                <picture>
                  <source srcSet={Item9Mobile} media="(max-width: 912px)"/>
                  <source srcSet={Item9Medium} media="(max-width: 1533px)"/>
                  <img src={Item9} alt="item"/>
                </picture>
              </div>
              <div className="clients__item">
              <picture>
                <source srcSet={Item10Mobile} media="(max-width: 912px)"/>
                <source srcSet={Item10Medium} media="(max-width: 1533px)"/>
                <img src={Item10} alt="item"/>
              </picture>
              </div>
          </div>
          </Slider>
        </div>
      </Wrapper>
    </div>
  )
}

export default Clients;
