import React from 'react'

import { ReactComponent as LogoLoupe } from '../../../svg/loupe.svg'
import './TraceForm.css'

const TraceForm = (props) => {
  const {
    className
  } = props;

  return (
    <form className={"trace " + (className ? className : '')}>
      <input className="trace__input" type='text' placeholder="   "/>
      <button className="trace__btn">
        <LogoLoupe className='trace__loupe-svg'/>
        <span> </span> 
      </button> 
    </form>
  )
}

export default TraceForm
